import React, { FC } from 'react';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  enumColumn,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { CLASS_NAME, CustomizacaoTermo } from '../../../../models/dtos/mcg/customizacaoTermo';
import { CUSTOMIZACAO_TERMO_ENUM_VALUES } from '../../../../models/enumerated/mcg/customizacaoTermoEnum';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'descricao',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

export interface CustomizacaoTermoSearchViewProps extends SearchViewProps<CustomizacaoTermo> {
  apenasCartaResponsabilidade?: boolean;
  apenasIndividualizadaVeracidade?: boolean;
  apenasDemonstracaoResultado?: boolean;
  apenasAberturaEncerramento?: boolean;
  apenasBalancoPatrimonial?: boolean;
}

const CustomizacaoTermoSearchView: FC<CustomizacaoTermoSearchViewProps> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'descricao',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
    enumColumn(
      {
        dataField: 'tipo',
        editable: false,
        filter: true,
        sort: true,
        text: 'Tipo',
      },
      CUSTOMIZACAO_TERMO_ENUM_VALUES
    ),
  ];

  return (
    <WindowComponent title="Manutenção de Termos de Abertura e Encerramento">
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'CustomizacaoTermoEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default CustomizacaoTermoSearchView;
